import {Component,OnInit,OnDestroy} from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {LoginStatusCheck} from "../../Service/loginfile"
import { ModalController } from 'ionic-angular';
import {ModalPage} from "../model/model";

@Component({
    templateUrl:"user.template.html",
})

export class UserComponent implements OnInit,OnDestroy{
	AllUserInfo=[];
	OpenForSellList=[];
	constructor(private _http: HttpClient,
		private _logger:LoginStatusCheck,
		public modalCtrl : ModalController){
	
		 var data = { message : 'hello world' };
         var modalPage = this.modalCtrl.create(ModalPage,data);
         modalPage.present();
         modalPage.onDidDismiss(data => {
      			console.log (data);
      			this._logger.loggerName=data.UserName
      			this.setData();
      		});
	}

	setData(){
		this._http.get('../assets/json/generated.json').subscribe((response:any) => {
			console.log(response[0].email)
			for(var i=0;i<response.length;i++){
				if(response[i].email === this._logger.loggerName+"@entropix.com"){
						this.AllUserInfo.push(response[i]);
					}
			}
			console.log(this.AllUserInfo);
		});
	}
	ngOnInit(){
		console.log("Here you are wellcome");
	}
	ngOnDestroy(){
		console.log("Thanks to visit us");
	}
	openSell(){

	}
	addToSellList(item){
		this.OpenForSellList.push(item);
	}
}


